"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

type HistoryItem = {
  type: "url" | "email";
  input: string;
  result: string;
};

export default function Home() {
  const router = useRouter();
  const [url, setUrl] = useState("");
  const [email, setEmail] = useState("");
  const [history, setHistory] = useState<HistoryItem[]>([]);

  // Load history from localStorage
  useEffect(() => {
    const saved = localStorage.getItem("history");
    if (saved) setHistory(JSON.parse(saved));
  }, []);

  // Save history to localStorage
  useEffect(() => {
    localStorage.setItem("history", JSON.stringify(history));
  }, [history]);

  const handleCheck = async () => {
  if (!url && !email) return;

  try {
    const res = await fetch("/api/detect", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ url, emailText: email }),
    });

    const data = await res.json();

    const newItem: HistoryItem = {
      type: url ? "url" : "email",
      input: url || email,
      result: data.verdict === "safe" ? "Safe ✅" : "Phishing ❌",
    };

    // Update state
    setHistory((prev) => {
      const updated = [newItem, ...prev];
      localStorage.setItem("history", JSON.stringify(updated)); // ✅ persist immediately
      return updated;
    });

    // Redirect
    router.push(
      `/result?type=${newItem.type}&input=${encodeURIComponent(
        newItem.input
      )}&verdict=${data.verdict}`
    );

    setUrl("");
    setEmail("");
  } catch (error) {
    console.error("Error checking phishing:", error);
  }
};

  return (
    <div className="min-h-screen bg-gray-50 p-6 flex flex-col items-center">
      {/* Input Section */}
      <Card className="w-full max-w-lg shadow-md mb-6">
        <CardContent className="p-6 flex flex-col gap-4">
          <h1 className="text-2xl font-bold text-center">
            🛡️ Phishing Detection
          </h1>

          <Input
            placeholder="Enter a URL..."
            value={url}
            onChange={(e) => setUrl(e.target.value)}
          />

          <Textarea
            placeholder="Paste suspicious email text..."
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />

          <Button onClick={handleCheck}>Check</Button>
        </CardContent>
      </Card>

      {/* History Section */}
      <Card className="w-full max-w-lg shadow-md">
        <CardContent className="p-6">
          <h2 className="text-xl font-semibold mb-4">History</h2>
          {history.length === 0 ? (
            <p className="text-gray-500 text-center">
              No history yet. Try checking something.
            </p>
          ) : (
            <ul className="space-y-2">
              {history.map((item, index) => (
                <li
                  key={index}
                  className="flex justify-between border-b py-2 text-sm"
                >
                  <span className="truncate max-w-[70%]">
                    {item.type === "url" ? "🌐" : "📧"} {item.input}
                  </span>
                  <span
                    className={
                      item.result.includes("Safe")
                        ? "text-green-600 font-medium"
                        : "text-red-600 font-medium"
                    }
                  >
                    {item.result}
                  </span>
                </li>
              ))}
            </ul>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
